/* src/Modal.css */
.Login-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.Login-modal-content {
  background: white;

  border-radius: 0.5vw;
  box-shadow: 0 0.1vw 0.5vw rgba(0, 0, 0, 0.1);
  position: relative;
}

.Login-modal-close {
  position: absolute;
  top: 0.8vw;
  right: 0.8vw;
  background: none;
  border: none;
  font-size: 1.3vw;
  cursor: pointer;
}
